import axios from 'axios';
import config from '../config';
// import fetchIntercept from 'fetch-intercept';

const conexpRest = (function () {
	let module = {};
	let self = module;

	module.standarOrderBy = '';
	module.baseUrlApiBack = '';
	module.suffix = '';
	module.userSSA = '';
	module.stopRequest = false;

	module.tokenAutorization = '';

	module.init = () => {
		self.initApiRest();
		self.initInterceptors();
	};

	module.initApiRest = () => {
		self.userSSA = config.getUserSSA();
		self.standarOrderBy = config.getRestParamsOrderBy();
		self.baseUrlApiBack = config.getBaseApiBack();
		self.suffix = config.getSuffixRest();
		self.stopRequest = false;
	};

	module.initInterceptors = () => {

		// const unregister = fetchIntercept.register({
		// 	request: function (url, config) {
		// 		// Modify the url or config here
		// 		console.log('fetchIntercept REQUEST: ',  url, config);
		// 		return [url, config];
		// 	},
			
		// 	requestError: function (error) {
		// 		console.log('fetchIntercept REQUESTERROR: ',  error);
		// 		// Called when an error occured during another 'request' interceptor call
		// 		return Promise.reject(error);
		// 	},

		// 	response: function (response) {
		// 		console.log('fetchIntercept RESPONSE: ',  response);
		// 		// Modify the reponse object
		// 		return response;
		// 	},

		// 	responseError: function (error) {
		// 		console.log('fetchIntercept RESPONSEERROR: ',  error);
		// 		// Handle an fetch error
		// 		return Promise.reject(error);
		// 	}
		// });
		
		// Unregister your interceptor
		// unregister();

		axios.interceptors.request.use(function (config) {
			console.log('axios.interceptors.request: ', config);
			if (!self.stopRequest) {
				config.headers['x-authorization'] = self.tokenAutorization;
				config.headers['REMOTE_USER'] = self.userSSA;
				config.headers['Access-Control-Allow-Credentials'] = true;
				config.headers['Access-Control-Allow-Origin'] = '*';
				return config;
			} else {
				new axios.Cancel('Operation canceled by the user.');
			}
		}, function (error) {
			console.log('axios.interceptors.request error: ', error);
			return Promise.reject(error);
		});

		axios.interceptors.response.use(function (response) {
			console.log('axios.interceptors.response: ', response);
			response.headers['x-authorization'] ? self.tokenAutorization = response.headers['x-authorization'] : '';
			return response;
		}, function (error) {
			console.log('axios.interceptors.response error: ', error);
			if (error.response && error.response.status === 401) {
				self.stopRequest = true;
			}
			return Promise.reject(error);
		});
	};

	/* CHAT */
	module.getUrlGetChatClient = (idClient) => {
		if (typeof idClient === 'undefined') {
			console.error('getUrlGetChatClient1: idClient is mandatory');
		}
		if (config.getEnvironment() !== 'mock') {
			return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy;
		}
		return '../mocks/messages.json';
	};

	/*Envo de un mensaje */
	module.getUrlSendMsgFromUserToClient = (idClient) => {
		if (typeof idClient === 'undefined') {
			console.error('getUrlSendMsgFromUserToClient: idClient is mandatory');
		}
		return self.baseUrlApiBack + 'chats/' + idClient + '/messages' + self.suffix + self.standarOrderBy;
	};

	module.getUrlSendParams = (idClient) => {
		if (typeof idClient === 'undefined') {
			console.error('getUrlSendParams: idClient is mandatory');
		}
		// Llamada intermedia Salesforce (Deprecated, ahora back se encarga)
		// if(config.getEnvironment() === 'epr') {
		// 	return 'http://pmf.cm.es/WebServicesAuxiliaresSalesforce/NotificarEventoChatSalesforce';
		// }
		// return 'http://vliamd063:58280/WebServicesAuxiliaresSalesforce/NotificarEventoChatSalesforce';
		
		return self.baseUrlApiBack + 'notificareventochatsalesforce' + self.suffix + self.standarOrderBy;
	};

	// module.getUrlCheckChatClient = (idClient)=> {
	// 	if ( typeof idClient === 'undefined' ) {
	// 		console.error('getUrlCheckChatClient: idClient is mandatory');
	// 	  }
	// 	// TODO cambiar cuando tengamos url definitiva
	// 	//return '../dist/mocks/servicios/check-client.json';
	// 	 return '../mocks/servicios/check-client.json';
	//   };
	/* END CHAT */

	/* FILES */
	module.getUrlAddFiles = () => {
		return self.baseUrlApiBack + 'files' + self.suffix;
	};

	module.getUrlFiles = () => {
		return self.baseUrlApiBack + 'files' + self.suffix;
	};

	module.getUrlSearchFiles = () => {
		return self.baseUrlApiBack + 'searchFiles' + self.suffix;
	};

	/* END FILES */

	return {
		init: module.init,
		getUrlGetChatClient: module.getUrlGetChatClient,
		// getUrlCheckChatClient: module.getUrlCheckChatClient,
		getUrlSendMsgFromUserToClient: module.getUrlSendMsgFromUserToClient,
		getUrlAddFiles: module.getUrlAddFiles,
		getUrlFiles: module.getUrlFiles,
		getUrlSearchFiles: module.getUrlSearchFiles,
		getUrlSendParams: module.getUrlSendParams
	}

})();

export default conexpRest;