import React from "react";
import ReactDOM from "react-dom";
import { render } from "react-dom";
import { Provider } from "react-redux";

import axios from "axios";
import config from "./app/config";
import { conexpRest, conexpUtils } from "./app/core/";

// Necesario para las Promesas de IE
require("babel-polyfill");

// This is the store (state).
import store from "./app/store";
import { clientChat } from "./app/actions/";

import "./styles/sass/main.scss";
import App from "./app/App";

import * as serviceWorker from "./serviceWorker";

/* Import to Translations */
import { addLocaleData, IntlProvider } from "react-intl";
import MESSAGES_ES from "./translations/es.js";
import propertiesJson from "./properties/properties.json";

// import jsonp from "jsonp";

// TODO
// ReactDOM.render(<App />, document.getElementById('conexp'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

/* START SETUP TRANSLATIONS*/

/* Import basic support for another locale if needed ('en' is included by default) */
let elLocaleData = require("react-intl/locale-data/es");
addLocaleData(elLocaleData);

/* Define start language */
const language = navigator.language.split(/[-_]/)[0];

/* Define your translations */
const messages = {
  es: MESSAGES_ES
};

/* END SETUP TRANSLATIONS */

const conexp = (function() {
  let module = {};
  let self = module;

  module.init = (data, handlerEvent) => {
	console.log('INIT: ', data, handlerEvent);
    config.init(propertiesJson, data);
    conexpRest.init();
    self.getToken(data, handlerEvent);
  };

  module.getToken = (data, handlerEvent) => {
    // withCredentials default
    axios.defaults.withCredentials = true;

    let url = conexpRest.getUrlGetChatClient(
      conexpUtils.setStringZero(data.clientId, 9)
    );

    /* Necesitamos hacer una primera llamada para setear el token que definirn los interceptores */
    axios
      .get(url, {
        params: {
          gidTipoOrigen: "73",
          gidCanal: "0353",
          gidUO: data.gidUO
        },
        // withCredentials: true
      })
      .then(response => {
        console.log("CORRECT llamada: ", response);
        self.initSynchronous(data, handlerEvent);
      })
      .catch(error => {
        console.log("ERROR llamada getToken ", error);
        let errorData = {
          hasError: true,
          msg: "Error al obtener el token"
        };
        store.dispatch(clientChat.clientChatFailure(errorData));
        self.initSynchronous(data, handlerEvent);
	  });
  };

  module.initSynchronous = (data, handlerEvent) => {
    self.renderApp(data, handlerEvent);
  };

  module.renderApp = (data, handlerEvent) => {
    render(
      <IntlProvider locale={language} messages={messages[language]}>
        <Provider store={store}>
          <App data={data} handlerEvent={handlerEvent} />
		  {/* <iframe src="https://iob-epd.cm.es/api/1.0/inicioEscenarioUsuario/neo/escenarioUsuario?x-j_gid_cod_app=e2&j_gid_action=login&j_gid_cod_app=ns&j_gid_cod_ds=ns&j_gid_idioma=ES&j_gid_cliente=&j_gid_tipo_origen=17&j_gid_origen=17000010000104++++++++&j_gid_canal=0010&j_gid_user_neo=A125335&j_gid_uo_terminal=000010000004&j_gid_uor=000010000004&j_gid_subproceso=https://iob-epd.cm.es/muro/"></iframe> */}
        </Provider>
      </IntlProvider>,
      document.getElementById("conexp")
    );
  };

  return {
    init: module.init
  };
})();

