import { conexpUtils } from '../core/';
import {
    MANAGER_DATA_LOADING,
    MANAGER_DATA_SUCCESS,
    MANAGER_DATA_FAILURE
} from '../actions/types';

const initialState = {};
let objAux = {};

initialState.loading = false;
initialState.error = conexpUtils.resetDataError();
initialState.data = [];

const manager = (state = initialState, action = {}) => {

    switch (action.type) {
        case MANAGER_DATA_LOADING:
            return Object.assign({}, state, { error: conexpUtils.resetDataError(), loading: true });
        case MANAGER_DATA_SUCCESS:
            // Descomentar para meter los datos del gestor en un array
            // return Object.assign({}, state, { data: [...state.data, action.payload], loading: false });
            return Object.assign({}, state, { data: action.payload, loading: false });
        case MANAGER_DATA_FAILURE:
            return Object.assign({}, state, { error: action.payload, loading: false });
        default:
            return state;
    }
}

export default manager;