import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CardAttachment } from '../../';
import { bindActionCreators } from 'redux';
import {
	files,
} from '../../../actions/';
class ClientAttachmentsList extends Component {

	constructor(props) {
		super(props);
		this.state = {
			fileName: ''
		};
		// this.onChange = this.onChange.bind(this);
	}

	render() {
		return (
			<div className='bc-attachments'>
				{
					/* loading */
					this.props.dataFiles && this.props.dataFiles.loading &&
					<div className='bc-loader bc-overlay'>
						<div className='bc-loader-item'></div>
					</div>
				}
				{
					/* data is loaded */
					this.props.dataFiles && !this.props.dataFiles.loading && this.props.dataFiles.errorGet && !this.props.dataFiles.errorGet.hasError && this.props.dataFiles.data &&
					<ul className='bc-attachments-list'>
						{
							this.props.dataFiles.data
								.filter((item) => { 
									return item.name.indexOf(this.props.filenameSearched) !== -1;
								})
								.map((item, index) => <CardAttachment
									key={index}
									index={index}
									downloadUrl={item.downloadUrl}
									time={item.time}
									date={item.date}
									size={item.sizeFormat}
									isClientFile={item.isClientFile}
									senderId={item.senderId}
									fileName={item.name}
									preview={item.preview} 
									type={item.type}
									getAttachmentType={this.getAttachmentType}/>)
						}
					</ul>
				}
			</div>
		);
	}
}

ClientAttachmentsList.propTypes = {
	dataFiles: PropTypes.object,
	filenameSearched: PropTypes.string,
	getAttachmentType: PropTypes.func
};

const mapStateToProps = state => {
	return {
		clientChat: state.clientChat,
		files: state.files,
		manager: state.manager
	};
};

const mapDispatchToProps = dispatch => ({
	searchFiles: bindActionCreators(files.searchFiles, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientAttachmentsList);
