import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { CONSTANTS } from '../../../core';
import {
	clientChat,
} from '../../../actions/';

class ClientChatHeader extends Component {

	constructor(props) {
		super(props);
		this.hideChat = this.hideChat.bind(this);
		this.onChangeSearcher = this.onChangeSearcher.bind(this);
	}

	hideChat() {
		this.props.clientChatUpdate(false);
	}

	onChangeSearcher(event) {
		this.props.handlerEventFilename(event.target.value)
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bc-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='bc-header'>
					<div className='bc-header-body'>
						<p className='bc-header-name'>
							{
								this.props.home.actualView === CONSTANTS.VIEWS.FILES &&
								<button className="bc-header-back" onClick={this.props.goBack}><i className="bc-ico bc-ico-left"></i></button>
							}
							{ this.props.manager.data.clientName ? this.props.manager.data.clientName : this.props.manager.data.clientId }
						</p>
					</div>

					{
						this.props.home.actualView === CONSTANTS.VIEWS.FILES &&
						<input type="text" className="bc-header-search" onKeyUp={() => this.onChangeSearcher(event)} placeholder='Buscar entre los archivos...' />
					}

					<button className="bc-header-close" aria-label="Cerrar" onClick={() => this.hideChat()}><i className="bc-ico bc-ico-close"></i></button>
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatHeader.propTypes = {
	goBack: PropTypes.func,
	handlerEventFilename: PropTypes.func
};

const mapStateToProps = state => {
	return {
		home: state.home,
		manager: state.manager
	};
};

const mapDispatchToProps = dispatch => ({
	clientChatUpdate: bindActionCreators(clientChat.clientChatUpdate, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatHeader);
