import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { Emojione } from 'react-emoji-render';
import classNames from 'classnames';

class ClientChatMessage extends Component {


	getFileName(attachment) {
		return (attachment && attachment.name) ? attachment.name.replace(".", "_").replace(" ", "") : attachment;
	}

	getDataAttachment(attachment, index) {
		let elementFromModernBrowser =
			<div>
				<a id="" className='bc-attach'
					key={index}
					href={attachment.downloadUrl}
					title={attachment.name}
					target="_blank"
					download
				>
					<i className={classNames('bc-ico bc-attach-file bc-ico-file', this.props.getAttachmentType(attachment.type))}></i>
					<i className='bc-ico bc-attach-download bc-ico-download'></i>

				</a>
			</div>;

		let elementFromIE =
			<div>
				<a id="" className='bc-attach'
					key={index}
					href={attachment.downloadUrl}
					title={attachment.name}
					target="_blank"
					download>
					<i className={classNames('bc-ico bc-attach-file bc-ico-file', this.props.getAttachmentType(attachment.type))}></i>
					<i className='bc-ico bc-attach-download bc-ico-download'></i>
				</a>

				<p className='bc-attach-note'>Pulse botn derecho "Guardar destino como..." si desea descargar el archivo</p>
			</div>;

		if (navigator.msSaveBlob) {
			return elementFromIE;
		} else {
			return elementFromModernBrowser;
		}

	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bc-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div id='chat-container-message' className='bc-main-message'>
					{
						this.props.chat.dateSeparate &&
						<time className='bc-message-date'>
							{this.props.chat.dateSeparate}
						</time>
					}
					{/* {
						this.props.chat.separateMsgClient && !this.props.chat.dateSeparate &&
						<hr className="bc-message-separate" />
					} */}
					<section className={classNames({ 'bc-message bc-message--in': this.props.chat.isUserMsg, 'bc-message bc-message--out': !this.props.chat.isUserMsg })}>
						<div className='bc-message-body'>
							{
								this.props.chat.hasAttachments &&
								this.props.chat.attachments.map((attachment, index) =>
									<div className={this.getFileName(attachment)} key={index}>

										{
											attachment &&
											this.getDataAttachment(attachment, index)
										}
									</div>
								)
							}
							{
								!this.props.chat.hasAttachments &&
								this.props.chat.message.split("<br />").map((data, key) => {
									return <Emojione key={key} className='bc-message-text' text={data} />;
								})
							}
						</div>
						<time className={classNames('bc-message-timestamp')}> {this.props.chat.time ? this.props.chat.time : this.props.chat.date}</time>
					</section>
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatMessage.propTypes = {
	key: PropTypes.number,
	chat: PropTypes.object,
	totalMsg: PropTypes.number,
	getAttachmentType: PropTypes.func
};

const mapStateToProps = state => {
	return {
	};
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatMessage);
