import axios from 'axios';
import {
	CLIENT_CHAT_VIEW_LOADING,
	CLIENT_CHAT_VIEW_UPDATE,
	CLIENT_CHAT_VIEW_FAILURE,
	CLIENT_CHAT_VIEW_SUCCESS,
	UPDATE_CHAT_LOADING,
	UPDATE_CHAT_SUCCESS,
	UPDATE_CHAT_FAILURE,
	CLIENT_CHAT_VIEW_MAXIMIZE,
	CLIENT_CHAT_VIEW_MINIMIZE
} from './types';
import { conexpRest, conexpUtils, conexpMaps } from '../core';
import LCC from 'lightning-container';

export const clientChat = (function () {
	let module = {};
	let self = module;

	module.clientChatLoading = () => {
		return {
			type: CLIENT_CHAT_VIEW_LOADING
		}
	};

	module.clientChatUpdate = (data) => {
		// Comunicamos a SalesForce el cierre o apertura del chat
		console.log('updateViewChat: ', data);
		LCC.sendMessage({name:'updateViewChat', value: data});
		return {
			type: CLIENT_CHAT_VIEW_UPDATE,
			payload: data
		}
	};

	module.clientChatMax = () => {
		return {
			type: CLIENT_CHAT_VIEW_MAXIMIZE
		}
	}

	module.clientChatMin = () => {
		return {
			type: CLIENT_CHAT_VIEW_MINIMIZE
		}
	}

	module.clientChatFailure = (data) => {
		return {
			type: CLIENT_CHAT_VIEW_FAILURE,
			payload: data
		}
	};

	module.clientChatSuccess = (data) => {
		return {
			type: CLIENT_CHAT_VIEW_SUCCESS,
			payload: data
		}
	};

	module.updateChatLoading = () => {
		return {
			type: UPDATE_CHAT_LOADING
		}
	};

	module.updateChatSuccess = (data) => {
		return {
			type: UPDATE_CHAT_SUCCESS,
			payload: data
		}
	};

	module.updateChatFailure = (data) => {
		return {
			type: UPDATE_CHAT_FAILURE,
			payload: data
		}
	};

	// module.checkChatClient = (clientId, userId) => {

	// 	return (dispatch) => { 
	// 		let url = conexpRest.getUrlCheckChatClient(clientId);
	// 		axios.get(url).then(response => {
	// 			if(response && response.statusText === 'OK')
	// 			{
	// 				dispatch(self.getChatClient(clientId, userId));
	// 			}
	// 			else{
	// 				console.log('error KO: response checkChatClient', response);
	// 				dispatch(self.clientChatFailure(errorData));
	// 			}
	// 		})
	// 			.catch((error) => {
	// 				console.log('error checkChatClient', error);
	// 				let errorData = {
	// 					hasError: true,
	// 					msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
	// 				};
	// 				dispatch(self.clientChatFailure(errorData));
	// 			});
	// 		}
	// 	};

	module.getChatClient = (clientId, userId) => {

		return (dispatch) => new Promise((resolve, reject) => {
			let url = conexpRest.getUrlGetChatClient(clientId);

			dispatch(self.clientChatLoading());

			axios.get(url).then(response => {

				//	setTimeout(() => {
				let dataMapped = [];

				/* Si cargamos de forma inicial los datos, les damos la vuelta (para mostrar abajo el ms reciente) y los mapeamos */
				dataMapped = conexpMaps.mapChatClient(response.data.reverse(), clientId, userId);

				dispatch(self.clientChatSuccess(dataMapped));
				resolve(dataMapped);
				// Bajamos el scroll hasta el final tras el envio del mensaje, fichero o audio
				setTimeout(() => {
					let boardContainer = document.querySelector('.bc-main-chat-view');
					if (boardContainer) {
						boardContainer.scrollTop = boardContainer.scrollHeight;
					}
				}, 400);
				//	}, 5000);

				/* Tenemos que enviarle a back, adems, un aviso de que hemos ledo todos los mensajes de este cliente */
				// if ( idLastMsgUnreaded ) {
				//   store.dispatch(unreadedChats.updateLastchatUnreaded(clientId, idLastMsgUnreaded));
				// }
			})
				.catch((error) => {
					console.log('error getChatClient', error);
					let errorData = {
						hasError: true,
						msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
					};
					dispatch(self.clientChatFailure(errorData));
				});
		});
	};

	module.sendMsgFromUserToClient = (chat, attachments, file) => {
		return (dispatch) => {
			let payload = {};
			let payloadParams = {};
			let url = conexpRest.getUrlSendMsgFromUserToClient(chat.clientId);
			let urlParams = conexpRest.getUrlSendParams(chat.clientId);
			payload.attachments = Array.isArray(attachments) ? attachments : [];
			payload.message = conexpUtils.cleanTags(chat.message);
			payloadParams = self.getPayloadSendData(chat, file);

			dispatch(self.updateChatLoading());

			axios.post(url, payload).then(response => {

				/* Insertamos el 'dateSeparate' o 'separateMsgClient' que nos viene de la vista */
				if (response && response.data) {
					response.data.dateSeparate = chat.dateSeparate ? chat.dateSeparate : '';
					response.data.separateMsgClient = chat.separateMsgClient ? chat.separateMsgClient : '';
				} else {
					/* Control de seguridad. Para no pintar el mensaje vaco en el caso de que la respuesta de back sea nula */
					return;
				}
				let dataMapped = conexpMaps.mapSingleMsgFromClientInChat(chat.clientId, chat.userId, response.data);
				dispatch(self.updateChatSuccess(dataMapped));

				// Doble llamada a Salesforce
				axios.get(urlParams, payloadParams).then(response => {
					console.log('response OK sendParams', response);
				})

				// TODO Comentado para que no aparezca el error al fallar la llamada
				//  .catch((error) => {
				// 	console.log('error sendParams', error);
				// 	let errorData = {
				// 		hasError: true,
				// 		msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
				// 	}
				// 	dispatch(self.updateChatFailure(errorData));
				// });

				// Bajamos el scroll hasta el final tras el envio del mensaje, fichero o audio
				setTimeout(() => {
					let boardContainer = document.querySelector('.bc-main-chat-view');
					if (boardContainer) {
						boardContainer.scrollTop = boardContainer.scrollHeight;
					}
				}, 400);
			})
				.catch((error) => {
					console.log('error sendMsgFromUserToClient', error);
					let errorData = {
						hasError: true,
						msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
					}
					dispatch(self.updateChatFailure(errorData));
				});
		}
	};

	module.sendMsgFromUserToClientMock = (dispatch, atachments) => {
		let temp = {};

		temp.attachments = atachments;
		temp.hasAttachments = true;
		temp.message = 'otro mensaje';
		temp.messageId = 12;
		temp.readed = true;
		temp.date = '2017-05-14';
		temp.time = '12:31.17';
		temp.showDate = false;
		temp.isUserMsg = true;
		temp.recipientId = 2
		temp.senderId = 1;

		dispatch(self.updateChatSuccess(temp));
	};

	module.getPayloadSendData = (chat, file) => {
		let params = {};

		params.ursus = chat.userId;
		params.gestor = chat.clientId;
		params.accion = 'EG';
		params.mensaje = conexpUtils.cleanTags(chat.message);
		// if(file) {
		// 	params.mensaje = file;
		// } else {
		// 	params.mensaje = new Blob([conexpUtils.cleanTags(chat.message)]);
		// }
		params.ticket = 'GEN';

		return params;
	};

	// module.blobToString =(b) =>{
	// 	var u, x;
	// 	u = URL.createObjectURL(b);
	// 	x = new XMLHttpRequest();
	// 	x.open('GET', u, false); // although sync, you're not fetching over internet
	// 	x.send();
	// 	URL.revokeObjectURL(u);
	// 	return x.responseText;
	// }

	return {
		clientChatLoading: module.clientChatLoading,
		clientChatUpdate: module.clientChatUpdate,
		clientChatSuccess: module.clientChatSuccess,
		clientChatFailure: module.clientChatFailure,
		clientChatMax: module.clientChatMax,
		clientChatMin: module.clientChatMin,
		chatHide: module.chatHide,
		updateChatLoading: module.updateChatLoading,
		updateChatSuccess: module.updateChatSuccess,
		updateChatFailure: module.updateChatFailure,
		getChatClient: module.getChatClient,
		checkChatClient: module.checkChatClient,
		sendMsgFromUserToClient: module.sendMsgFromUserToClient,
		sendMsgFromUserToClientMock: module.sendMsgFromUserToClientMock,
		getPayloadSendData: module.getPayloadSendData
	};

})();
