import {
    ADD_FILE_LOADING,
    ADD_FILE_SUCCESS,
    ADD_FILE_FAILURE,
    GET_FILES_LOADING,
    GET_FILES_SUCCESS,
    GET_FILES_FAILURE,
    DOWNLOAD_FILE_LOADING,
    DOWNLOAD_FILE_SUCCESS,
    DOWNLOAD_FILE_FAILURE
} from '../actions/types';
import { conexpUtils } from '../core/';

const initialState = {};
initialState.data = [];

initialState.loadingAdd = false;
initialState.errorAdd = {};
initialState.errorAdd.hasError = false;
initialState.errorAdd.msg = '';

initialState.loadingGet = false;
initialState.errorGet = {};
initialState.errorGet.hasError = false;
initialState.errorGet.msg = '';

initialState.dataDownload = {}; // creo que este al final no se va a necesitar
initialState.loadingDownload = false;
initialState.errorDown = {};
initialState.errorDown.hasError = false;
initialState.errorDown.msg = '';

const files = (state = initialState, action = {}) => {
    switch (action.type) {

        /* ADD */
        case ADD_FILE_LOADING:
            return Object.assign({}, state, { errorAdd: conexpUtils.resetDataError(), loadingAdd: true });
        case ADD_FILE_SUCCESS:
            return Object.assign({}, state, { data: action.payload, loadingAdd: false });
        case ADD_FILE_FAILURE:
            return Object.assign({}, state, { errorAdd: action.payload, loadingAdd: false });

        /* DOWNLOAD */
        case DOWNLOAD_FILE_LOADING:
            return Object.assign({}, state, { errorDown: conexpUtils.resetDataError(), loadingDownload: true });
        case DOWNLOAD_FILE_SUCCESS:
            return Object.assign({}, state, { dataDownload: action.payload, loadingDownload: false });
        case DOWNLOAD_FILE_FAILURE:
            return Object.assign({}, state, { errorDown: action.payload, loadingDownload: false });

        /* GET */
        case GET_FILES_LOADING:
            return Object.assign({}, state, { errorGet: conexpUtils.resetDataError(), loadingGet: true });
        case GET_FILES_SUCCESS:
            return Object.assign({}, state, { data: action.payload, loadingGet: false });
        case GET_FILES_FAILURE:
            return Object.assign({}, state, { data: [], errorGet: action.payload, loadingGet: false });
        default:
            return state;
    }
}

export default files;