import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import classNames from 'classnames';

class ClientChatSend extends Component {

	constructor(props) {
		super(props);
		this.state = {
			showOptionsFile: false
		};
		this.changeStateOptionsFile = this.changeStateOptionsFile.bind(this);
		this.selectSendFiles = this.selectSendFiles.bind(this);
	}

	changeStateOptionsFile() {
		this.setState({showOptionsFile: !this.state.showOptionsFile});
	}

	selectSendFiles() {
		this.changeStateOptionsFile();
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='bc-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className={classNames('bc-footer', { 'bc-footer--open': this.state.showOptionsFile })}>
					<div className="bc-footer-prev">
						<div className="bc-footer-type">
							<textarea
								disabled={this.props.disabled}
								onChange={this.props.eventChangeMessage}
								name={this.props.nameMessage}
								value={this.props.valueMessage}
								className='bc-footer-typearea' rows='1'
								placeholder='Enviar mensaje'
							></textarea>
						</div>

						<button
							onClick={this.props.eventClickMessage}
							className={classNames('bc-footer-send', { 'bc-footer-send--disabled': this.props.disabled })}
							disabled={this.props.disabled}
							aria-label='Enviar'
						>
							<i className="bc-ico bc-ico-send"></i>
						</button>

						<button className="bc-footer-filebutton" onClick={() => this.changeStateOptionsFile()}><i className="bc-ico bc-ico-paperclip"></i></button>

						<div className="bc-footer-filebox">
							<label htmlFor="file" className="bc-footer-link">Enviar fichero</label>
							<input
								disabled={this.props.disabled}
								onChange={this.props.eventChangeFile}
								onClick={this.selectSendFiles}
								name={this.props.nameFile}
								value={this.props.valueFile}
								id='file'
								type='file'
								hidden
							/>

							<button
								onClick={this.props.eventClickShowFiles}
								className={classNames('bc-footer-link', { 'disabled': this.props.disabled })}
								disabled={this.props.disabled}
							>Ver ficheros</button>
						</div>
					</div>
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatSend.propTypes = {
	disabled: PropTypes.bool,
	nameMessage: PropTypes.string,
	valueMessage: PropTypes.string,
	eventClickMessage: PropTypes.func,
	eventClickShowFiles: PropTypes.func,
	eventChangeMessage: PropTypes.func,
	nameFile: PropTypes.string,
	valueFile: PropTypes.string,
	eventChangeFile: PropTypes.func
};

const mapStateToProps = state => {
	return {
	};
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatSend
);
