import {
    MANAGER_DATA_LOADING,
    MANAGER_DATA_SUCCESS,
    MANAGER_DATA_FAILURE
} from './types';

export const manager = (function () {
    let module = {};
    module.managerDataLoading = () => {
        return {
            type: MANAGER_DATA_LOADING
        }
    };

    module.managerDataSuccess = (data) => {
        return {
            type: MANAGER_DATA_SUCCESS,
            payload: data
        }
    };

    module.managerDataFailure = (data) => {
        return {
            type: MANAGER_DATA_FAILURE,
            payload: data
        }
    };

    return {
        managerDataLoading: module.managerDataLoading,
        managerDataUpdate: module.managerDataUpdate,
        managerDataFailure: module.managerDataFailure,
        managerDataSuccess: module.managerDataSuccess
    };

})();