import PropTypes from 'prop-types';
import React, { Component } from 'react';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { files } from '../../../actions/';
import { ClientAttachmentsList } from '../../';

class ClientAttachmentsView extends Component {

	constructor(props) {
		super(props);
		this.state = {
			clientId: this.props.manager.data.clientId
		};
	}

	componentWillMount() {
		this.props.getFiles(this.state.clientId);
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName="bc-animation-appear"
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<ClientAttachmentsList
					dataFiles={this.props.files}
					filenameSearched={this.props.filenameSearched}
					getAttachmentType={this.getAttachmentType}
				/>
				{
					this.props.files && (this.props.files.errorGet && this.props.files.errorGet.hasError) &&
					<div className='bc-main bc-main-chat-view bc-main--noscroll'>
						<aside className='bc-overlay bc-overlay--error'>
							<div className='bc-overlay-content'>
								<i className="bc-overlay-circle bc-ico bc-ico-alert"></i>

								<h2 className='bc-overlay-title'>Oops! Tenemos un problema</h2>

								<p className='bc-overlay-txt'>Error al recuperar los archivos</p>

								<button className='bc-overlay-btn' onClick={this.props.goBack}>Volver</button>
							</div>
						</aside>
					</div>
				}
			</CSSTransitionGroup>
		);
	}
}

ClientAttachmentsView.propTypes = {
	goBack: PropTypes.func,
	filenameSearched: PropTypes.string,
	getAttachmentType: PropTypes.func
};

const mapStateToProps = state => {
	return {
		files: state.files,
		clientData: state.clientData,
		unreadedCalls: state.unreadedCalls,
		manager: state.manager
	};
};

const mapDispatchToProps = dispatch => ({
	getFiles: bindActionCreators(files.getFiles, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientAttachmentsView);
