import conexpUtils from './conexp-utils';
import config from '../config'

const conexpMaps = (function () {
	const module = {};
	const self = module;

	/* CHAT */
	module.mapChatClient = (response, clientId, userId) => {
		let chatClient = {};
		let dateShown = [];
		let typeClientMsg = [];

		chatClient.items = response.map((item) => {

			/* Insertamos el valor "dateSeparate" para separar los mensajes del da correspondiente */
			let dateAux = conexpUtils.getDateFromTimestamp(item.timestamp);
			item.dateSeparate = '';
			if (dateAux && dateShown.indexOf(dateAux) === -1) {
				dateShown.push(dateAux);
				if (dateAux === conexpUtils.getDateFromTimestamp(Date.now())) {
					dateAux = 'HOY';
				}
				item.dateSeparate = dateAux;
			}

			/* Insertamos el valor "separateMsgClient" para separar los mensajes propios de los recibidos */
			item.separateMsgClient = false;
			typeClientMsg.push(item.senderId);
			if (typeClientMsg && typeClientMsg.length > 1) {
				item.separateMsgClient = false;
				if (item.senderId !== typeClientMsg[typeClientMsg.length - 2]) {
					item.separateMsgClient = true;
				}
			}
			return self.mapSingleMsgFromClientInChat(clientId, userId, item);
		});

		return chatClient.items;
	};

	module.mapSingleMsgFromClientInChat = (clientId, userId, item) => {

		let temp = {};
		let dateNow = conexpUtils.getDateFromTimestamp(new Date());

		temp.attachments = self.getAttachments(item.attachments);
		temp.hasAttachments = Array.isArray(item.attachments) && item.attachments.length ? true : false;
		temp.message = item.message || '';
		temp.messageId = item.messageId;
		temp.readed = true; // Lo seteamos siempre como true, ya que se solicita desde chat
		temp.date = conexpUtils.getDateFromTimestamp(item.timestamp);
		temp.time = conexpUtils.getTimeFromTimestamp(item.timestamp);
		temp.timestamp = item.timestamp;
		temp.showDate = temp.date === dateNow ? false : true;
		temp.isUserMsg = self.getIsUserMsg(clientId, userId, item.senderId, item.recipientId);
		temp.senderId = item.senderId;
		temp.recipientId = item.recipientId;
		temp.isFile = self.checkIsFileByContentMsg(temp.message);
		temp.dateSeparate = item.dateSeparate;
		temp.separateMsgClient = item.separateMsgClient;

		temp.message = conexpUtils.cleanTags(temp.message);
		temp.message = temp.message.replace(/(?:\r\n|\r|\n)/g, '<br />');

		return temp;
	};

	module.getIsUserMsg = (clientId, userId, senderId, recipientId) => {
		if (userId === senderId) {
			return true;
		} else if (clientId === recipientId) {
			return false;
		} else {
			return null;
		}
	};

	module.getAttachments = (_attachments_) => {
		if (!_attachments_) {
			return _attachments_;
		}
		let attachments = _attachments_.map((item) => {
			item.downloadUrl = self.getFileToDownload(item.downloadUrl);
			return item;
		})
		return attachments;
	};

	module.getFileToDownload = (partialRoute) => {
		if (config.getEnvironment() === 'dev') {
			return config.getBaseApiBackFiles() + partialRoute + "?&TGT=xxxx&REMOTE_USER=A100301";
		} else {
			return config.getBaseApiBackFiles() + partialRoute;
		}
	};

	module.checkIsFileByContentMsg = (msg) => {
		if (msg.indexOf(config.getStringFileToserver()) !== -1) {
			return true;
		}
		return false;
	};

	/* END CHAT */

	/* FILES */
	module.mapGetFiles = (response, clientId) => {

		let filesUser = {};
		filesUser.items = response.map((item) => {
			let temp = {};
			temp.fileId = item.fileId;
			temp.name = item.name;
			temp.downloadUrl = self.getFileToDownload(item.downloadUrl);
			temp.preview = item.preview;
			temp.size = item.size;
			temp.sizeFormat = conexpUtils.getSizeFromBytes(item.size);
			temp.date = conexpUtils.getDateFromTimestamp(item.uploadDate);
			temp.time = conexpUtils.getTimeFromTimestamp(item.uploadDate);
			temp.senderId = item.senderId;
			temp.isClientFile = item.senderId == clientId ? true : false;
			temp.type = item.type;

			return temp;
		});

		return filesUser.items;
	};
	/* END FILES */

	return {
		mapChatClient: module.mapChatClient,
		mapSingleMsgFromClientInChat: module.mapSingleMsgFromClientInChat,
		mapGetFiles: module.mapGetFiles,
		mapGetAllFiles: module.mapGetAllFiles
	};

})();

export default conexpMaps;
