const config = (function () {
	let module = {};
	let self = module;

	module.PROPERTIES = {};

	module.init = (properties, generalData) => {
		self.PROPERTIES = properties;
		self.PROPERTIES.ENVIRONMENT = generalData.environment;
		self.PROPERTIES.USERSSA = generalData.userSSA;
	};

	/*** PROPERTIES ***/

	/* Environment */
	module.getEnvironment = () => {
		return self.PROPERTIES.ENVIRONMENT;
	};

	/* User */
	module.getUserSSA = () => {
		return self.PROPERTIES.USERSSA;
	};

	/* Files */
	module.getFilesMaxUpload = () => {
		return self.PROPERTIES.FILES.MAX_UPLOAD;
	};
	module.getFilesErrorType = () => {
		return self.PROPERTIES.FILES.ERROR_FILE_TYPE;
	}
	module.getFilesTypes = () => {
		return self.PROPERTIES.FILES.TYPES;
	};
	module.getFilesErrorSizeMsg = () => {
		return self.PROPERTIES.FILES.ERROR_SIZE_MSG;
	};
	module.getFilesErrorNameMsg = () => {
		return self.PROPERTIES.FILES.ERROR_NAME_MSG;
	};
	module.getFilesDeleteMsgError = () => {
		return self.PROPERTIES.FILES.DELETE_MSG_ERROR;
	};
	/* Partcula que se aade en el mensaje cuando
	enviamos un archivo para saber que es un archivo */
	module.getStringFileToserver = () => {
		return 'Archivo: ';
	};
	/* End Files */
	module.getRestParamsOrderBy = () => {
		return self.PROPERTIES.REST_PARAMS.STANDAR_ORDERBY;
	};

	/* Api Rest */
	module.getBaseApiBack = () => {
		switch (self.getEnvironment()) {
			case 'mock':
				//	return '../dist/mocks/';
				return '../mocks/';
			case 'dev':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_DEV + self.getBaseApiRest();
			case 'epd':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPD + self.getBaseApiRest();
			case 'epi1':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI1 + self.getBaseApiRest();
			case 'epi2':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI2 + self.getBaseApiRest();
			case 'piloto':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_PILOTO + self.getBaseApiRest();
			case 'emi':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EMI + self.getBaseApiRest();
			case 'epr':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPR + self.getBaseApiRest();
			default:
				console.error('error: not environment selected!');
		}
	};

	module.getBaseApiBackFiles = () => {
		switch (self.getEnvironment()) {
			case 'mock':
				return '../mocks/';
			case 'dev':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_DEV;
			case 'epd':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPD;
			case 'epi1':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI1;
			case 'epi2':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPI2;
			case 'piloto':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_PILOTO;
			case 'emi':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EMI;
			case 'epr':
				return self.PROPERTIES.ROUTES.BASE_API_BACK_EPR;
			default:
				console.error('error: not environment selected!');
		}
	};

	module.getBaseApiRest = () => {
		return self.PROPERTIES.ROUTES.BASE_API_REST_SALESFORCE;
	};

	module.getSuffixRest = () => {

		// Para Salesforce TGT tiene que por URL y el REMOTE_USER por el header

		let queryStringTGT = 'xxxx';
		let queryStringRemoteUser = self.PROPERTIES.USERSSA;
		// let queryStringRemoteUser = self.getParameterByName('REMOTE_USER');
		// let queryStringTipoOrigen = self.getParameterByName('j_gid_tipo_origen');
		let suffix = '';

		// if (config.getEnvironment() === 'mock') {
		// 	return '.json';
		// } else if (config.getEnvironment() === 'dev') {
		// 	queryStringTGT = 'xxxx';
			// queryStringRemoteUser = 'A100301';
		// }

		// if (!queryStringTGT && queryStringRemoteUser) {
		// 	return;
		// }

		suffix += '?';
		suffix += queryStringTGT ? '&TGT=' + queryStringTGT : '';
		suffix += queryStringRemoteUser ? '&REMOTE_USER=' + queryStringRemoteUser : '';
		return suffix;
	};

	module.getParameterByName = (name) => {
		let url = window.location.href;
		let regex, results;

		name = name.replace(/[\[\]]/g, '\\$&');
		regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)');
		results = regex.exec(url);

		if (!results || !results[2]) {
			return null;
		}

		return decodeURIComponent(results[2].replace(/\+/g, ' '));
	};

	/* End Api Rest */

	return {
		init: module.init,
		getEnvironment: module.getEnvironment,
		getUserSSA: module.getUserSSA,
		getBaseApiBack: module.getBaseApiBack,
		getFilesMaxUpload: module.getFilesMaxUpload,
		getFilesTypes: module.getFilesTypes,
		getFilesErrorType: module.getFilesErrorType,
		getFilesErrorSizeMsg: module.getFilesErrorSizeMsg,
		getFilesErrorNameMsg: module.getFilesErrorNameMsg,
		getFilesDeleteMsgError: module.getFilesDeleteMsgError,
		getStringFileToserver: module.getStringFileToserver,
		getBaseApiBackFiles: module.getBaseApiBackFiles,
		getRestParamsOrderBy: module.getRestParamsOrderBy,
		getSuffixRest: module.getSuffixRest,
		getParameterByName: module.getParameterByName
	};

})();

export default config;
