import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import classNames from 'classnames';
import { CONSTANTS, conexpUtils } from './core';
import '../styles/sass/main.scss';
import {
	clientChat,
	home,
	manager
} from './actions/';

import {
	HomeView
} from './components/';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {
			sendValue: false
		};
	}

	componentWillMount() {
		/* Mostramos el home para que se pueda mostrar el 'bullet' */
		this.props.homeUpdate(true);
		console.log('this.props.handlerEvent', this.props.handlerEvent);
	
		if (this.props.handlerEvent) {
			this.props.clientChatUpdate(true);
			this.props.changeView(CONSTANTS.VIEWS.CHAT);
		} else {
			this.props.clientChatUpdate(false);
		}
		
		
		this.props.managerDataSuccess({
			clientId: conexpUtils.setStringZero(this.props.data.clientId, 9),
			userId: conexpUtils.setStringZero(this.props.data.userId, 5),
			gidUO: this.props.data.gidUO,
			clientName: this.props.data.clientName,
			userSSA: this.props.data.userSSA
		});

	}

	componentWillReceiveProps(nextProps) {
		if(nextProps.handlerEvent && !this.props.handlerEvent) {
			console.log('Actualiza el chat');
			this.props.clientChatUpdate(true);
			this.props.changeView(CONSTANTS.VIEWS.CHAT);
			this.checkNotification();
		}
	}

	checkNotification() {
		// Simulamos que llega un mensaje:
		// 1.- Se podr hacer haciendo que se actualice simplemente la conversacin
		this.props.getChatClient(conexpUtils.setStringZero(this.props.data.clientId, 9), conexpUtils.setStringZero(this.props.data.userId, 5));
	}

	render() {
		return (
			<div className={classNames('App')}>
				{
					this.props.home.show &&
					<HomeView />
				}
			</div>
		);
	}
}

App.propTypes = {
	handlerEvent: PropTypes.any,
	data: PropTypes.object
};

const mapStateToProps = state => {
	return {
		clientChat: state.clientChat,
		home: state.home,
		manager : state.manager
	};
};

const mapDispatchToProps = dispatch => ({
	homeUpdate: bindActionCreators(home.homeUpdate, dispatch),
	changeView: bindActionCreators(home.changeView, dispatch),
	getChatClient: bindActionCreators(clientChat.getChatClient, dispatch),
	managerDataSuccess: bindActionCreators(manager.managerDataSuccess, dispatch),
	clientChatUpdate: bindActionCreators(clientChat.clientChatUpdate, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
