import PropTypes from 'prop-types';
import React, { Component } from 'react';
import classNames from 'classnames';

class CardAttachment extends Component {
	
	getAttachmentType(type) {
        if (type.indexOf('pdf') != -1) {
            return 'bc-attach-file--pdf';
        } else if (type.indexOf('word') != -1) {
            return 'bc-attach-file--doc';
        } else if (type.indexOf('sheet') != -1) {
            return 'bc-attach-file--xls';
        } else if (type.indexOf('presentation') != -1) {
            return 'bc-attach-file--ppt';
        } else if (type.indexOf('image') != -1) {
            return 'bc-attach-file--img';
        } else {
            return 'bc-attach-file--text'
        }
    }

	render() {
		return (
			<li className='bc-attach bc-attach--list'>
				<div className="bc-attach-row">
					<i className={classNames('bc-ico bc-attach-file bc-ico-file', this.getAttachmentType(this.props.type))}></i>
					<span className="bc-attach-name">{this.props.fileName}</span>

					{/* Sin el "download" abre directamente el fichero */}
					<a
						href={this.props.downloadUrl}
						download
						target="_blank"
						key={this.props.index}
					>
						<i className='bc-ico bc-attach-download bc-ico-download'></i>
					</a>
				</div>

				<div className="bc-attach-row">
					<i className={classNames('bc-attach-ico bc-ico', { 'bc-ico-arrow-down': this.props.isClientFile, 'bc-ico-arrow-up': !this.props.isClientFile })}></i>

					<span className='bc-attach-state'>{this.props.isClientFile ? 'Recibido' : 'Enviado'}</span>

					<time className='bc-attach-time'>{this.props.date} {this.props.time}</time>

					<span className='bc-attach-size'>{this.props.size}</span>
				</div>
			</li>
		);
	}
}

CardAttachment.propTypes = {
	fileName: PropTypes.string,
	downloadUrl: PropTypes.string,
	isClientFile: PropTypes.bool,
	size: PropTypes.string,
	date: PropTypes.string,
	time: PropTypes.string,
	type: PropTypes.string,
	getAttachmentType: PropTypes.func
};

export default CardAttachment;
