import {
	HOME_VIEW_LOADING,
	HOME_VIEW_UPDATE,
	HOME_VIEW_FAILURE,
	HOMELIST_VIEW_LOADING,
	HOMELIST_VIEW_UPDATE,
	HOMELIST_VIEW_FAILURE,
	CHANGE_VIEW
} from '../actions/types';
import { conexpUtils } from '../core/';
  
const initialState = {};
let objAux = {};

initialState.loading = false;
initialState.error = conexpUtils.resetDataError();
initialState.show = false;
initialState.actualView = '';
initialState.homeList = {};
initialState.homeList.loading = false;
initialState.homeList.error = conexpUtils.resetDataError();
initialState.homeList.show = false;

const home = (state = initialState, action = {}) => {

	switch (action.type) {
		case CHANGE_VIEW:
			return Object.assign({},  state, { actualView: action.payload } );
		case HOME_VIEW_LOADING:
			return Object.assign({},  state, { error: conexpUtils.resetDataError(), loading: true} );
		case HOME_VIEW_UPDATE:
			return Object.assign({},  state, { show: action.payload, loading: false } );
		case HOME_VIEW_FAILURE:
			return Object.assign({},  state, { error: action.payload, loading: false } );
		case HOMELIST_VIEW_LOADING:
			objAux = {};
			objAux.loading = true;
			objAux.error = conexpUtils.resetDataError();
			return Object.assign({},  state, { homeList: objAux } );
		case HOMELIST_VIEW_UPDATE:
			objAux = {};
			objAux.loading = false;
			objAux.show = action.payload;
			return Object.assign({},  state, { homeList: objAux } );
		case HOMELIST_VIEW_FAILURE:
			objAux = {};
			objAux.loading = false;
			objAux.error = action.payload;
			return Object.assign({},  state, { homeList: objAux } );

		default:
			return state;
	}
}

export default home;