import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import config from '../../../config';
import { CONSTANTS } from '../../../core';
import {
	ClientChatMessage,
	ClientChatSend,
	ClientChatHeader,
	ClientAttachmentsView
} from '../../../components/';
import {
	clientChat,
	files,
	home
} from '../../../actions/';
import { conexpUtils } from '../../../core';

class ClientChatView extends Component {

	constructor(props) {
		super(props);

		this.controlCounter = null;

		this.state = {
			textareaMessage: '',
			textIsEmpty: false,
			disabledSend: false,
			loadingUpdate: true,
			fileMessage: '',
			clientId: '',
			userId: '',
			chatView: true,
			filesView: false,
			filenameSearched: ''
		};

		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleSubmitMessage = this.handleSubmitMessage.bind(this);
		this.checkInputDisabled = this.checkInputDisabled.bind(this);
		this.handleFileInput = this.handleFileInput.bind(this);
		this.handleCloseBtnClick = this.handleCloseBtnClick.bind(this);
		this.handleMinBtnClick = this.handleMinBtnClick.bind(this);
		this.handleMaxBtnClick = this.handleMaxBtnClick.bind(this);
		this.showViewFiles = this.showViewFiles.bind(this);
		this.showChatView = this.showChatView.bind(this);
		this.closeModalError = this.closeModalError.bind(this);
		this.closeModalErrorFile = this.closeModalErrorFile.bind(this);
		this.handlerEventFilename = this.handlerEventFilename.bind(this);
	}

	componentWillMount() {
		this.setState({
			clientId: this.props.manager.data.clientId,
			userId: this.props.manager.data.userId
		}, function () {
			this.getDataChat();
		});
	}

	componentWillReceiveProps(nextProps) {
		/* Controlamos el tiempo de respuesta del servidor para deshabilitar el envio entre medias */
		this.setState({ loadingUpdate: nextProps.clientChat.loading });

		if (this.props.clientChat.loadingUpdate && !nextProps.clientChat.loadingUpdate && nextProps.clientChat.errorUpdate.hasError) {
			/* Error al intentar mandar un mensaje. En este caso no vaciamos el contenido del textarea */
		} else if (this.props.clientChat.loadingUpdate && !nextProps.clientChat.loadingUpdate && !nextProps.clientChat.errorUpdate.hasError) {
			this.setState({ textareaMessage: '', textIsEmpty: true });
		}
	}

	getDataChat() {
		this.props.getChatClient(this.state.clientId, this.state.userId);
	}

	handleFileInput(event) {
		let fileUpload;

		/* Por si se sube un fichero de audio, reseteamos la disposicin de que la vista esta mostrada al completo (para las precargas de audios) */
		// this.props.updateLoadAudio(false);

		console.log("handleFileInput event: ", event);
		if (event.target) {
			console.log("handleFileInput event.target: ", event.target);
		}
		if (event.target.files) {
			console.log("handleFileInput event.target.files: ", event.target.files);
		}
		if (event.target.files && event.target.files.length > 0) {
			console.log("handleFileInput event.target.files[0]: ", event.target.files[0]);
		}

		/* Aunque de momento enviamos directament el archivo, por si ms adelante se
		necesita, lo guardamos tb en el estado */
		this.handleInputChange(event);

		/* Comprobamos el tamao y el nombre del archivo que se intenta enviar */
		if (this.checkSizeFile(event.target.files[0]) && !this.checkErrorFileName(event.target.files[0]) && !this.checkInvalidFileType(event.target.files[0])) {
			/* Envo */
			fileUpload = event.target.files[0];
			this.props.addFiles(fileUpload, this.state.clientId, this.state.userId, this.props.sendMsgFromUserToClient);
		}

		/* Finalmente, reseteamos */
		this.setState({ fileMessage: '' });
	}

	checkSizeFile(file) {
		if (file.size / 1000 > config.getFilesMaxUpload()) {
			let errorData = {
				hasError: true,
				msg: config.getFilesErrorSizeMsg()
			}
			this.props.addFilesFailure(errorData);
			return false;
		} else {
			return true;
		}
	}

	checkErrorFileName(file) {
		if (conexpUtils.checkTags(file.name)) {
			let errorData = {
				hasError: true,
				msg: config.getFilesErrorNameMsg()
			}
			this.props.addFilesFailure(errorData);
			return true;
		} else {
			return false;
		}
	}

	checkInvalidFileType(file) {
		if (file.name.toLowerCase().indexOf('.exe') >= 0 ||
			file.name.toLowerCase().indexOf('.bat') >= 0 ||
			file.name.toLowerCase().indexOf('.vbs') >= 0 ||
			file.name.toLowerCase().indexOf('.com') >= 0 ||
			file.name.toLowerCase().indexOf('.dll') >= 0) {
			let errorData = {
				hasError: true,
				msg: config.getFilesErrorType()
			}
			this.props.addFilesFailure(errorData);
			return true;
		} else {
			return false;
		}
	}

	checkInputDisabled() {
		/* Mientras espera la respuesta del servidor, deshabilitamos el envio de mensajes */
		if (this.state.loadingUpdate) {
			return true;
		}
	}

	handleInputChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		});

		if (event.target.name === 'textareaMessage' && event.target.value) {
			this.setState({ textIsEmpty: false })
		} else {
			this.setState({ textIsEmpty: true })
		}
	}

	showViewFiles() {
		this.setState({ filesView: true, chatView: false });
		this.props.changeView(CONSTANTS.VIEWS.FILES);
	}
	showChatView() {
		setTimeout(() => {
			let boardContainer = document.querySelector('.bc-main-chat-view');
			if (boardContainer) {
				boardContainer.scrollTop = boardContainer.scrollHeight;
			}
		}, 400);

		this.setState({ filesView: false, chatView: true });
		this.props.changeView(CONSTANTS.VIEWS.CHAT);
	}

	handleSubmitMessage(event) {
		let chat = {};
		let attachments = [];

		if (!this.state.textareaMessage || this.state.textareaMessage.trim() === '') {
			this.setState({ textareaMessage: '', textIsEmpty: true });
			return;
		}
		chat.message = this.state.textareaMessage;
		chat.clientId = this.state.clientId;
		chat.timestamp = Date.now();
		chat.dateSeparate = '';
		chat.separateMsgClient = false;
		chat.userId = this.state.userId;

		/* Comprobamos si haba comunicacin anterior para indicar o no el dateSeparate */
		if ((this.props.clientChat.data && this.props.clientChat.data.length > 0 &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1] &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1].date !== conexpUtils.getDateFromTimestamp(chat.timestamp)) ||
			!this.props.clientChat.data[this.props.clientChat.data.length - 1]) {
			chat.dateSeparate = 'HOY';
		}

		/* Comprobamos si el mensaje anterior era propio para crear separacin o no */
		if (this.props.clientChat.data && this.props.clientChat.data.length > 0 &&
			this.props.clientChat.data[this.props.clientChat.data.length - 1] &&
			!this.props.clientChat.data[this.props.clientChat.data.length - 1].isUserMsg) {
			chat.separateMsgClient = true;
		}

		this.props.sendMsgFromUserToClient(chat, attachments);
	}

	handlerEventFilename(filename) {
		this.setState({ filenameSearched: filename });
	}

	handleCloseBtnClick() {
		this.props.clientChatUpdate(false);
	}

	handleMinBtnClick() {
		this.props.clientChatMin();
	}

	handleMaxBtnClick() {
		this.props.clientChatMax();
	}

	closeModalError() {
		// Limpiamos el error
		let errorData = {
			hasError: false,
			msg: ''
		}
		this.props.clientChatFailure(errorData);
		this.props.updateChatFailure(errorData);

		// Intentamos recargar la informacin
		this.getDataChat();
	}

	closeModalErrorFile() {
		let errorData = {
			hasError: false,
			msg: ''
		}
		this.props.addFilesFailure(errorData);

		// Intentamos recargar la informacin
		this.getDataChat();
	}
	getAttachmentType(type) {
		//const classFile = 'bc-ico bc-attach-file bc-ico-file';
		if (type.indexOf('pdf') != -1) {
			return 'bc-attach-file--pdf';
		} else if (type.indexOf('word') != -1) {
			return 'bc-attach-file--doc';
		} else if (type.indexOf('sheet') != -1) {
			return 'bc-attach-file--xls';
		} else if (type.indexOf('presentation') != -1) {
			return 'bc-attach-file--ppt';
		} else if (type.indexOf('image') != -1) {
			return 'bc-attach-file--img';
		} else {
			return 'bc-attach-file--text'
		}
	}

	render() {
		if (this.props.clientChat.minimized) {
			return (
				<button onClick={this.handleMaxBtnClick}>Maximizar</button>
			)
		}
		return (
			<CSSTransitionGroup
				transitionName='bc-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				{/* Pantalla 07 - Dashboard Usuario Carterizado - 1 */}
				<div className="bc-chat">
					{/* Header */}
					<ClientChatHeader
						goBack={this.showChatView}
						handlerEventFilename={this.handlerEventFilename}
					/>

					{/* Messages */}
					<div className='bc-main bc-main-chat-view'>
						{
							this.props.clientChat && this.props.clientChat.loading &&
							/* Loading Component */
							<div className='bc-loader bc-overlay'>
								<div className='bc-loader-item'></div>
							</div>
						}
						{
							/* Load data */
							this.state.chatView && this.props.clientChat && this.props.clientChat.data && !this.props.clientChat.error.hasError && !this.props.clientChat.errorUpdate.hasError && !this.props.files.errorAdd.hasError &&
							this.props.clientChat.data.map((chat, index) => <ClientChatMessage
								key={index}
								disabled={this.checkInputDisabled()}
								chat={chat}
								totalMsg={this.props.clientChat.data.length}
								getAttachmentType={this.getAttachmentType}
							/>)
						}
						{
							this.state.chatView && this.props.clientChat && ((this.props.clientChat.error && this.props.clientChat.error.hasError) || (this.props.clientChat.errorUpdate && this.props.clientChat.errorUpdate.hasError)) &&
							<aside className='bc-overlay bc-overlay--error'>
								<div className='bc-overlay-content'>
									<i className="bc-overlay-circle bc-ico bc-ico-alert"></i>

									<h2 className='bc-overlay-title'>Oops! Tenemos un problema</h2>

									<p className='bc-overlay-txt'>{(this.props.clientChat.errorUpdate && this.props.clientChat.errorUpdate.hasError) ? ' Error al enviar el mensaje' : 'Error al recuperar la conversacin'}</p>

									<button className='bc-overlay-btn' onClick={this.closeModalError}>Reintentar</button>
								</div>
							</aside>
						}

						{
							this.state.chatView && this.props.files && ((this.props.files.errorAdd && this.props.files.errorAdd.hasError)) &&
							<aside className='bc-overlay bc-overlay--error'>
								<div className='bc-overlay-content'>
									<i className="bc-overlay-circle bc-ico bc-ico-alert"></i>

									<h2 className='bc-overlay-title'>Oops! Tenemos un problema</h2>

									<p className='bc-overlay-txt'>{this.props.files.errorAdd && this.props.files.errorAdd.msg ? this.props.files.errorAdd.msg : 'Error al aadir el archivo'}</p>

									<button className='bc-overlay-btn' onClick={this.closeModalErrorFile}>Aceptar</button>
								</div>
							</aside>
						}

						{/* Files */}
						{
							this.state.filesView &&
							<ClientAttachmentsView
								goBack={this.showChatView}
								filenameSearched={this.state.filenameSearched}
								getAttachmentType={this.getAttachmentType}
							/>
						}
					</div>

					{/* Footer */}
					{
						this.props.home.actualView === CONSTANTS.VIEWS.CHAT &&
						<ClientChatSend
							nameMessage='textareaMessage'
							disabled={this.checkInputDisabled()}
							valueMessage={this.state.textareaMessage}
							eventClickMessage={this.handleSubmitMessage}
							eventClickShowFiles={this.showViewFiles}
							eventChangeMessage={this.handleInputChange}
							nameFile='fileMessage'
							valueFile={this.state.fileMessage}
							eventChangeFile={this.handleFileInput}
						/>
					}
				</div>
			</CSSTransitionGroup>
		);
	}
}

ClientChatView.propTypes = {

};

const mapStateToProps = state => {
	return {
		clientChat: state.clientChat,
		files: state.files,
		manager: state.manager,
		home: state.home,
	};
};

const mapDispatchToProps = dispatch => ({
	getChatClient: bindActionCreators(clientChat.getChatClient, dispatch),
	getChatClient: bindActionCreators(clientChat.getChatClient, dispatch),
	sendMsgFromUserToClient: bindActionCreators(clientChat.sendMsgFromUserToClient, dispatch),
	addFiles: bindActionCreators(files.addFiles, dispatch),
	addFilesFailure: bindActionCreators(files.addFilesFailure, dispatch),
	clientChatFailure: bindActionCreators(clientChat.clientChatFailure, dispatch),
	updateChatFailure: bindActionCreators(clientChat.updateChatFailure, dispatch),
	clientChatUpdate: bindActionCreators(clientChat.clientChatUpdate, dispatch),
	clientChatMax: bindActionCreators(clientChat.clientChatMax, dispatch),
	clientChatMin: bindActionCreators(clientChat.clientChatMin, dispatch),
	changeView: bindActionCreators(home.changeView, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(ClientChatView);
