import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import classNames from 'classnames';

class Bullet extends Component {

    
	constructor (props) {
        super(props);
       
	}

    render() {
            return (
                <CSSTransitionGroup
                    transitionName='conexp-animation-appear'
                    transitionAppear={true}
                    transitionAppearTimeout={500}
                    transitionEnter={false}
                    transitionLeave={false}>
                    <div onClick={ this.props.triggerNotificationList } className={classNames('bc-bullet', {'bc-bullet--open': this.props.clientChat.show})}>
                        <i className={classNames('bc-ico', { 'bc-ico-chat': !this.props.clientChat.show, 'bc-ico-close': this.props.clientChat.show })}></i>
                    </div>
                </CSSTransitionGroup>
            );
    }
}

Bullet.propTypes = {
    triggerNotificationList: PropTypes.func
};

const mapStateToProps = state => {
	return {
        clientChat: state.clientChat
    };
};

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(Bullet);
