import React, { Component } from 'react';
import { connect } from 'react-redux';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { bindActionCreators } from 'redux';
import { CONSTANTS } from '../../../core';
import {
	clientChat,
	home
} from '../../../actions/';
import {
	Bullet,
	ClientChatView,
} from '../../../components/';

class HomeView extends Component {

	constructor(props) {
		super(props);
		this.triggerNotificationList = this.triggerNotificationList.bind(this);
	}

	/* Mostramos/Ocultamos el chat */
	triggerNotificationList() {
		this.props.clientChatUpdate(!this.props.clientChat.show);
		this.props.changeView(!this.props.clientChat.show ? CONSTANTS.VIEWS.CHAT : CONSTANTS.VIEWS.FILES);
	}

	render() {
		return (
			<CSSTransitionGroup
				transitionName='conexp-animation-appear'
				transitionAppear={true}
				transitionAppearTimeout={500}
				transitionEnter={false}
				transitionLeave={false}>
				<div className='module-home' id='home-container'>
					{
						this.props.clientChat.show &&
						<div className='home-section-conexp'>
							<ClientChatView />
						</div>
					}
					{ /* Bola que abre el panel */}
					<Bullet
						triggerNotificationList={this.triggerNotificationList}
					/>
				</div>
			</CSSTransitionGroup>
		);
	}
}

HomeView.propTypes = {

};

const mapStateToProps = state => {
	return {
		home: state.home,
		notification: state.notification,
		clientChat: state.clientChat,
		manager: state.manager
	};
};

const mapDispatchToProps = dispatch => ({
	clientChatUpdate: bindActionCreators(clientChat.clientChatUpdate, dispatch),
	changeView: bindActionCreators(home.changeView, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(HomeView);
