import axios from 'axios';
import config from '../config';
import {
	conexpMaps,
	conexpRest,
	conexpUtils
} from '../core';
import {
	ADD_FILE_LOADING,
	ADD_FILE_SUCCESS,
	ADD_FILE_FAILURE,
	GET_FILES_LOADING,
	GET_FILES_SUCCESS,
	GET_FILES_FAILURE
} from './types';

export const files = (function () {
	let module = {};
	let self = module;

	module.addFilesLoading = () => {
		return {
			type: ADD_FILE_LOADING
		}
	};

	module.addFilesSuccess = (data) => {
		return {
			type: ADD_FILE_SUCCESS,
			payload: data
		}
	};

	module.addFilesFailure = (data) => {
		return {
			type: ADD_FILE_FAILURE,
			payload: data
		}
	};

	module.addFiles = (file, clientId, userId, fbSendMsg) => {
		return (dispatch) => {
			let data = new FormData();
			let url = conexpRest.getUrlAddFiles();

			data.append('file', file);
			data.append('clientId', clientId);
			// Descomentar en el caso de mandar a mano el duration del audio
			// if(audioDuration) {
			//   data.append('audioDuration', audioDuration);
			// }

			dispatch(self.addFilesLoading());

			if (config.getEnvironment() === 'mock') {
				self.addFilesMock(dispatch);
			} else {
				axios.post(url, data).then(response => {
					let atachments = self.mapAddFiles(response.data);
					let newMsg = {};

					newMsg.message = config.getStringFileToserver() + file.name;
					newMsg.clientId = clientId;
					newMsg.userId = userId;
					newMsg.timestamp = Date.now();

					dispatch(self.addFilesSuccess(atachments));
					fbSendMsg(newMsg, atachments, file);

				})
					.catch((error) => {
						console.log('error addFiles', error);
						let errorData = {
							hasError: true,
							msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
						};
						dispatch(self.addFilesFailure(errorData));
					});
			}
		}
	};

	module.mapAddFiles = (response) => {
		return [response];
	};

	module.getFilesLoading = () => {
		return {
			type: GET_FILES_LOADING
		}
	};

	module.getFilesSuccess = (data) => {
		return {
			type: GET_FILES_SUCCESS,
			payload: data
		}
	};

	module.getFilesFailure = (data) => {
		return {
			type: GET_FILES_FAILURE,
			payload: data
		}
	};

	module.getFiles = (clientId) => {

		return (dispatch) => {

			let url = conexpRest.getUrlFiles();

			dispatch(self.getFilesLoading());
			axios.get(url, {
				params: {
					clientId: clientId,
					sortBy: 'uploadDate'
				}
			}).then(response => {
				if (response && response.data) {
					response.data.sort(function (a, b) {
						return new Date(b.uploadDate) - new Date(a.uploadDate);
					});
				}
				let dataMapped = conexpMaps.mapGetFiles(response.data, clientId);
				dispatch(self.getFilesSuccess(dataMapped));

			})
				.catch((error) => {
					let errorData = {
						hasError: true,
						msg: conexpUtils.setMsgError(error.response ? error.response.status : '')
					};
					dispatch(self.getFilesFailure(errorData));
				});
		}
	};

	module.searchFiles = (data) => {
		return (dispatch) => {
			let url = conexpRest.getUrlSearchFiles();
			axios.get(url, {
				params: {
					clientId: data.userId,
					idGestor: data.managerId,
					nombre: data.name,
					sortBy: 'uploadDate'
				}
			}).then((response) => {

				console.log('Bsqueda realizada con xito');
				let dataMapped = conexpMaps.mapGetFiles(response.data)
				dispatch(self.getFilesSuccess(dataMapped));
			}).catch((error) => {
				console.log('Error en la bsqueda realizada', error);
			});
		}
	}


	return {
		getFiles: module.getFiles,
		addFiles: module.addFiles,
		addFilesFailure: module.addFilesFailure,
		searchFiles: module.searchFiles
	};

})();
